.headtop {
  width: 100%;
  position: relative;
  top: 0;
  background: #28303b;
  opacity: 1;
  z-index: 9999999;
}

.headtop .info__head {
  padding: 0px;
  color: #f2f2f2;
}

@media screen and (min-width: 1024px) {
  .headtop .info__head {
    padding-top: 10px;
    display: flex;
    justify-content: space-between;
  }
}

.headtop .info__head .info__head__contact {
  display: none;
}

@media screen and (min-width: 1024px) {
  .headtop .info__head .info__head__contact {
    display: flex;
    flex-direction: row;
    padding: 5px 0px;
  }
}

.headtop .info__head .info__head__contact span {
  display: flex;
  justify-content: center;
  flex-flow: row wrap;
  font-family: "robotolight";
  font-size: 1em;
}

@media screen and (min-width: 1024px) {
  .headtop .info__head .info__head__contact span {
    margin-right: 5px;
  }
}

.headtop .info__head .info__head__contact span i {
  font-style: normal;
  margin-left: 5px;
  position: relative;
  top: 2px;
}

.headtop .info__head .info__head__social {
  padding-top: 5px;
  display: none;
}

@media screen and (min-width: 1024px) {
  .headtop .info__head .info__head__social {
    display: flex;
    justify-content: center;
    flex-flow: row wrap;
  }
}

.headtop .info__head .info__head__social a {
  color: #ffffff;
  text-decoration: none;
}

@media screen and (min-width: 1024px) {
  .headtop .info__head .info__head__social a {
    font-size: 1em;
    margin-bottom: 10px;
  }
}

.headtop .info__head .info__head__social a:nth-child(2) {
  margin-left: 10px;
}

.main-header {
  background: #28303b;
  position: relative;
  z-index: 9999;
}

@media screen and (min-width: 1024px) {
  .main-header {
    height: 80px;
  }
}

@media screen and (min-width: 1024px) {
  .main-header .l-container .head__header {
    display: flex;
    justify-content: space-between;
