@font-face {
  font-family: "css-desde-cero";
  src:url("../fonts/fonts/css-desde-cero.eot");
  src:url("../fonts/fonts/css-desde-cero.eot?#iefix") format("embedded-opentype"),
    url("../fonts/fonts/css-desde-cero.woff") format("woff"),
    url("../fonts/fonts/css-desde-cero.ttf") format("truetype"),
    url("../fonts/fonts/css-desde-cero.svg#css-desde-cero") format("svg");
  font-weight: normal;
  font-style: normal;

}

[data-icon]:before {
  font-family: "css-desde-cero" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "css-desde-cero" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

